import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import { useEffect, useState } from 'react';
import {colorsRef} from '../../../../consts/colorConstants';
import { styled } from '@mui/material/styles';
import AddIcon from '@mui/icons-material/Add';
import CloseIcon from '@mui/icons-material/Close';
import IconButton from '@mui/material/IconButton';
import DialogTitle from '@mui/material/DialogTitle';
import {forwardRef} from 'react';
import Slide from '@mui/material/Slide';
import { StoreInput } from '../../../inputs/tableInput';
import {Typography  } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { ColorPicker } from '../../../inputs/colorInput';
import {SelectInput} from '../../../inputs/select';
import {IsAcceptedInput} from '../../../inputs/isAccepted';
import { CustomizedCheckboxInfo, CustomizedCheckboxDelivery } from '../../../inputs/checkBox'; 
import {orderStatusThunk, getValidationForm, getAllStatuses} from '../../../../redux/asyncOrders';
import { StyledNumInput} from '../../../inputs/number';
import { StyledInput } from '../../../inputs/textfield';
import {clearStatusState, modalOpenUpdate} from '../../../../redux/statusReduser';
import {SimpleSnackbar} from '../../../alerts/alertStatus';
import {StatusesSelectInput} from '../../../inputs/statusses';
import { ColorButton, buttonStyle, textStyle, inputGroupStyle } from './styles';


const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="down" ref={ref} {...props} />;
  });

export function AddStatusForm() {
  const isValid = useSelector((state) => state.ordersAll.isValid);
  const modalOpen = useSelector((state) => state.addStatus.modalOpen);
  const dispatch = useDispatch();

  useEffect(()=>{
if (isValid) {
console.log('is valid');
  dispatch(orderStatusThunk())

  handleClose()
} 
  },[isValid])
  
    function BootstrapDialogTitle(props) {
        const { children, onClose, ...other } = props;      
        return (
          <DialogTitle sx={{backgroundColor: colorsRef.modalHeaderBgColor,
           color: colorsRef.modalBodyBgColor,
            fontSize: '19px',
             alignItems: 'center',
             width: '400px'
            }}
             {...other}>
            {children}
            {onClose ? (
              <IconButton
                aria-label="close"
                onClick={onClose}
                sx={{
                  position: 'absolute',
                  right: 8,
                  top: 12,
                  color: (theme) => theme.palette.grey[500],
                }}
              >
                <CloseIcon />
              </IconButton>
            ) : null}
          </DialogTitle>
        );
      }
      const BootstrapDialog = styled(Dialog)(({ theme }) => ({
        '& .MuiDialogContent-root': {
          padding: theme.spacing(2),
        },
        '& .MuiDialogActions-root': {
          padding: theme.spacing(1),
        },
      }));  


const handleStatusesUpdate =()=>{
dispatch(getValidationForm())
     
  }

  const handleClickOpen = () => {
    dispatch(modalOpenUpdate(true))
  };

  const handleClose = () => {
    dispatch(clearStatusState())
    dispatch(modalOpenUpdate(false))
  };



 
  return (
    <div>
      <ColorButton startIcon={<AddIcon sx={{ '&.MuiIcon-root': {fontSize: '20px'}}}/>} variant="contained" size="small" onClick={handleClickOpen}>
         
      </ColorButton>

      <BootstrapDialog  TransitionComponent={Transition} keepMounted open={modalOpen} >
        <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}  >
              
        </BootstrapDialogTitle>
        
        <List sx={{width: '100%', padding: '20px'}}>

          <ListItem key='name' sx={inputGroupStyle}> <Typography sx={textStyle} > :</Typography> 
          <StyledInput />
          </ListItem>

          <ListItem key='num' sx={inputGroupStyle}> <Typography sx={textStyle} >  :</Typography>
          <StyledNumInput />
          </ListItem>

          <ListItem sx={inputGroupStyle}> <Typography sx={textStyle} > :</Typography>
           <ColorPicker/>
            </ListItem>

          <ListItem sx={inputGroupStyle}> <Typography sx={textStyle} >  :</Typography> 
          <SelectInput /> 
          </ListItem>
          <ListItem sx={inputGroupStyle}> <Typography sx={textStyle} >   :</Typography>
           <StoreInput type='store' />
            </ListItem>
          <ListItem sx={inputGroupStyle}> <Typography sx={textStyle} > :</Typography>
           <IsAcceptedInput /> 
           </ListItem>
          <ListItem sx={inputGroupStyle}> <Typography sx={textStyle} > :</Typography>
           <CustomizedCheckboxDelivery  />
            </ListItem>
          <ListItem sx={inputGroupStyle}> <Typography sx={textStyle} >  :</Typography>
           <CustomizedCheckboxInfo  />
            </ListItem>
          <ListItem sx={inputGroupStyle}> <Typography sx={textStyle} > :</Typography> 
          <StatusesSelectInput />
           </ListItem>
          </List>
        <DialogActions  sx={{alignSelf: 'center', dicplay: 'block', marginBottom: '20px'}}>
          <Button sx={buttonStyle} onClick={handleStatusesUpdate}></Button>
          <Button sx={buttonStyle} onClick={handleClose}></Button>
        </DialogActions>
      </BootstrapDialog>
      <SimpleSnackbar/>
    </div>
  );
}
